/*--------------------------------------------------------------
# Normalize
--------------------------------------------------------------*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  border: 0;
  margin: 0;
  outline: 0;
  padding: 0;
  vertical-align: baseline;
}

html {
  -webkit-font-smoothing: none;
  text-rendering: optimizeLegibility !important;
  -webkit-font-smoothing: antialiased !important;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  overflow-x: hidden;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 18px;
  line-height: 1.6;
  font-weight: 400;
  background-color: var(--body-bg-color);
  color: var(--text-color);
}

.wp-block-quote,
.wp-block-pullquote {
  border-color: var(--block-quote-color);
}
.wp-block-quote p,
.wp-block-pullquote p {
  font-size: 20px;
  line-height: 1.5;
}

.container {
  max-width: 720px;
  margin-right: auto;
  margin-left: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.container-fluid {
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}

img {
  border-style: none;
  max-width: 100%;
  height: auto;
}

h1 {
  font-size: 40px;
  line-height: 1.2;
  margin-bottom: 30px;
}
@media (max-width: 767px) {
  h1 {
    font-size: 28px;
    line-height: 1.3;
    margin-bottom: 15px;
  }
}

h2 {
  font-size: 28px;
  line-height: 36px;
  margin-bottom: 30px;
}
@media (max-width: 767px) {
  h2 {
    font-size: 22px;
    line-height: 24px;
    margin-bottom: 15px;
  }
}

h3 {
  margin-bottom: 30px;
}
@media (max-width: 767px) {
  h3 {
    margin-bottom: 15px;
  }
}

a {
  color: var(--link-color);
}
a:hover {
  color: var(--link-hover-color);
}
a:focus {
  outline: 1px dashed var(--header-nav-link-color);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--heading-color);
  margin-bottom: 15px;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block;
}

a:active {
  outline: 0;
}

figure {
  margin: 0;
}

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}

input[type=search], input[type=text], input[type=password] {
  padding: 8px;
  flex-grow: 1;
  min-width: 3em;
  border: 1px solid #949494;
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
}

input[type=submit],
button[type=submit] {
  background: #f7f7f7;
  border: 1px solid #ccc;
  padding: 8px 0.625em;
  color: #32373c;
  margin-left: 0.625em;
  word-break: normal;
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
  cursor: pointer;
}

.not-found input.search-submit {
  cursor: pointer;
  border-radius: 24px;
  font-size: 14px;
  line-height: 160%;
  padding: 8px 0px;
  min-width: 120px;
  text-align: center;
  background-color: #333333;
  color: #fff;
  font-weight: bold;
  border-color: #333;
}
.not-found input.search-field {
  border: 1px solid #DEDEDE;
  border-radius: 4px;
  height: 45px;
  font-size: 14px;
}

.gpwp-header {
  padding: 11px 0 10px;
  background-color: var(--header-bg-color);
}
.gpwp-header-wrap {
  display: flex;
  flex-direction: row;
  flex-grow: 1;
  width: 100%;
  gap: 20px;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.gpwp-header .site-branding {
  display: inline-flex;
  align-items: center;
  text-align: left;
  flex-shrink: 0;
  gap: 20px;
}
@media (max-width: 767px) {
  .gpwp-header .site-branding {
    gap: 8px;
  }
}
.gpwp-header .site-branding a {
  line-height: normal;
  flex-shrink: 0;
  text-decoration: underline;
}
.gpwp-header .site-branding a img {
  height: 75px;
  width: auto;
}
@media (max-width: 767px) {
  .gpwp-header .site-branding a img {
    height: 55px;
  }
}
.gpwp-header .site-branding .site-title {
  font-size: 20px;
  line-height: 1.2em;
  word-wrap: break-word;
  font-weight: 700;
  text-transform: capitalize;
  color: var(--site-title-color);
}
.gpwp-header .site-branding .site-title a {
  color: var(--site-title-color);
  text-decoration: none;
}
.gpwp-header .site-branding .site-description {
  margin-top: 3px;
  line-height: 20.16px;
  text-transform: capitalize;
  color: var(--site-tagline-color);
}
@media (max-width: 767px) {
  .gpwp-header .site-branding .site-description {
    font-size: 15px;
    line-height: 18px;
    margin-top: 0px;
  }
}

.gpwp-menu ul {
  position: relative;
}
.gpwp-menu ul li {
  position: relative;
  display: inline-block;
  margin-right: 15px;
  padding-right: 20px;
}
.gpwp-menu ul li::after {
  content: "/";
  position: absolute;
  top: 0;
  right: 0;
  color: var(--header-nav-link-color);
}
@media (max-width: 767px) {
  .gpwp-menu ul li {
    margin-right: 10px;
    padding-right: 15px;
  }
}
.gpwp-menu ul li a {
  font-size: 16px;
  font-weight: 500;
  line-height: 26px;
  text-transform: uppercase;
  color: var(--header-nav-link-color);
  position: relative;
  z-index: 999;
  text-decoration: underline 1.5px;
  text-decoration-color: var(--header-nav-link-color);
  -moz-text-decoration-color: var(--header-nav-link-color);
}
.gpwp-menu ul li:last-child {
  margin-right: 0;
  padding-right: 0;
}
.gpwp-menu ul li:last-child::after {
  display: none;
}

.gpwp-header-menu-left .gpwp-header-wrap {
  flex-direction: row-reverse;
}
.gpwp-header-menu-left .site-branding {
  flex-direction: row-reverse;
}

.gpwp-header-menu-bottom .gpwp-header-wrap {
  flex-direction: column;
  align-items: flex-start;
  gap: 5px;
}

@media screen and (min-width: 400px) {
  .gpwp-footer-menu-right .site-info {
    flex-direction: row-reverse;
  }
}

.header-nav-wrap {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}
.header-nav-wrap .menu-text-link {
  color: var(--header-nav-link-color);
  font-weight: bold;
  text-decoration: none;
}

@media screen and (max-width: 767px) {
  .main-navigation {
    flex-basis: calc(100% - 40px);
  }
}

.gpwp-theme-header__search-container {
  position: relative;
  display: inline-block;
}
@media screen and (max-width: 767px) {
  .gpwp-theme-header__search-container {
    flex-basis: 32px;
  }
}
.gpwp-theme-header__search-container:focus-within .gpwp-theme-header__search-overlay {
  width: 100%;
  /* Adjust as needed */
  opacity: 1;
  left: 0;
}
.gpwp-theme-header__search-container.is-active .gpwp-theme-header__search-overlay {
  width: 100%;
  opacity: 1;
  left: 0;
}
.gpwp-theme-header__search-container button {
  background-color: transparent;
  border: 0;
  color: currentColor;
  cursor: pointer;
}
.gpwp-theme-header__search-container button svg {
  position: relative;
  top: 4px;
}
.gpwp-theme-header__search-container .search-form {
  display: flex;
}
.gpwp-theme-header__search-container .search-form label {
  width: 310px;
}
@media (max-width: 480px) {
  .gpwp-theme-header__search-container .search-form label {
    width: 205px;
  }
}
.gpwp-theme-header__search-container .search-submit {
  margin: 0;
  color: #fff;
  border: none;
  background-color: #333333;
  border-radius: 0 6px 6px 0;
  font-size: 16px;
  padding: 8px 20px;
}
.gpwp-theme-header__search-overlay {
  width: 0;
  position: fixed;
  top: 0;
  left: -100%;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10000;
  backdrop-filter: blur(3px);
  transition: width 0.3s ease, opacity 0.3s ease, padding 0.3s ease;
  opacity: 0;
}
.gpwp-theme-header__search-overlay input[type=search] {
  width: 100%;
  height: 60px;
  padding: 15px;
  background-color: transparent;
  border: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  color: currentColor;
  outline: none;
  border-radius: 6px 0 0 6px;
}
@media (max-width: 480px) {
  .gpwp-theme-header__search-overlay input[type=search] {
    height: 50px;
  }
}
.gpwp-theme-header__inner-search-overlay {
  display: flex;
  max-width: 100%;
  background-color: #fff;
  border-radius: 6px;
}